<template>
  <div class="page graybg page-home">
    <div class="balanceBg">
      <div class="balanceBox">
        <div class="welcomeEl">
          欢迎
          <span v-if="!$root.userInfo.realName" class="name" @click="$router.push('/user_certify')">
            请先实名
          </span>
          <span v-else class="name">{{ $root.userInfo.realName }}</span>
        </div>
        <div class="balanceEl" @click="refreshBalance">
          <h3 class="balanceLable">
            <span> 余额 </span>
            <Refresh class="refreshBalance" :loading="isLoadingBalance"/>
          </h3>
          <div class="balance">
