          </div>
          <div class="label">卖单余额</div>
        </van-grid-item>
        <van-grid-item class="border_right_1px">
          <div class="balanceEl">
            <span class="balance">
              {{ parseFloat($root.userInfo.sumFrozenAmount).toFixed(2) }}
            </span>
            <span class="unit"> V币 </span>
          </div>
          <div class="label">交易中</div>
        </van-grid-item>
      </van-grid>
      <div class="btnsBox">
        <van-button color="#F0535F" to="/sale">卖币</van-button>
        <van-button color="#6F59FE" replace to="/trade/transaction">买币</van-button>
        <span class="split"></span>
        <van-button color="#1890ff" to="/user_transfer">转账</van-button>
      </div>
    </div>

    <van-notice-bar
      :text="announcement.length ? announcement[announcementIndex].content : '暂无公告内容！'"
      background="#fff"
      color="#333"
      scrollable
      @replay="handleReplay"
    >
      <span slot="left-icon" class="icon icon-notice"></span>
    </van-notice-bar>

    <!-- 轮播 -->
    <van-swipe
      v-if="carousels.length"
      class="carousel"
      :autoplay="3000"
      indicator-color="white"
      @change="(v) => (carouselIndex = v)"
    >
      <van-swipe-item v-for="item in carousels" :key="item.id">
        <img
          class="carouselImg"
          :src="$root.getImageUrl(item.imgAddr)"
          :alt="item.title"
          @click="handleCarousel(item)"
        />
      </van-swipe-item>
    </van-swipe>

    <div class="hangOrder border_top_1px">
      <div class="title">
        <span>我的挂单</span>
        <span @click="$router.push('/my_hang_orders')">更多</span>
      </div>
      <HangOrderItem v-for="(item, index) in hangOrders" :key="index" :dt="item" />
    </div>
    <div class="tansactionOrder border_top_1px">
      <div class="title">
        <span>交易记录</span>
        <span @click="$router.replace('/orders/list')">更多</span>
      </div>
      <TransactionItem v-for="(item, index) in orders" :key="index" :dt="item" />
    </div>
    <FloatBtn />
  </div>
</template>

<script>
import { Grid, GridItem } from "vant";
import { getCarouselChartInfo } from "@/apis/user";
import { myOrderList, myPendingOrderList } from "@/apis/market";
import { getAnnouncement } from "@/apis/common";
import { HangOrderItem, TransactionItem } from "./components";
import FloatBtn from "@/components/FloatBtn.vue";
import Refresh from '@/components/Refresh.vue'

export default {
  components: {
    HangOrderItem,
    TransactionItem,
    [Grid.name]: Grid,
    [GridItem.name]: GridItem,
    FloatBtn,
    Refresh
  },
  data() {
    return {
      hangOrders: [],
      orders: [],
      isLoadingBalance: false,
      announcement: [],
      announcementIndex: 0,
      carousels: [],
      carouselIndex: 0,
    };
  },
  created() {
    this.$root.updateUnReadCount();
    this.getCarouselChartInfo();
    this.loadHangOrders();
    this.loadOrders();
    this.$root.$emit("root:set-topbar", ["user-center", "help", "notices", "customer-service"]);
    this.$root.$emit("root:set-layout", { bottom: true });
    this.getAnnouncement();
    if (
      this.$root.showAnnouncementDialog === false &&
      this.$root.config.alertAnn &&
      this.$root.config.alertAnn.status == "1"
    ) {
      this.$dialog.alert({
        className: "homeAnnouncementDialog",
        title: this.$root.config.alertAnn.title || "通知",
        message: `<div class="richText">${this.$root.config.alertAnn.content}</div>`,
        allowHtml: true,
        beforeClose(action, done) {
          this.$root.showAnnouncementDialog = true;
          done();
        },
      });
    }
  },
  methods: {
    handleCarousel(item) {
      this.$router.push(`/rich_text/${item.id}/carousel`);
    },
    async getCarouselChartInfo() {
      const { ok, data } = await getCarouselChartInfo();
      if (ok) {
        this.carousels = data;
      }
    },
    handleReplay() {
      this.announcementIndex += 1;
      if (this.announcementIndex >= this.announcement.length) {
        this.announcementIndex = 0;
      }
    },
    async getAnnouncement() {
      const { ok, msg, data } = await getAnnouncement({
        page: 1,
        pageSize: 20,
      });
      if (ok) {
        this.announcementIndex = 0;
        this.announcement = data.list;
      } else {
        this.announcement = [];
      }
    },
    async refreshBalance() {
      this.isLoadingBalance = true;
      await this.$root.fetchUserInfo();
      this.isLoadingBalance = false;
    },
    loadHangOrders() {
      myPendingOrderList({
        page: 1,
        pageSize: 3,
      }).then(({ ok, msg, data }) => {
        if (ok) {
          this.hangOrders = data.list;
        } else {
          this.$toast.fail(msg);
        }
      });
    },
    loadOrders() {
      myOrderList({
        page: 1,
        pageSize: 3,
      }).then(({ ok, msg, data }) => {
        if (ok) {
          this.orders = data.list;
        } else {
          this.$toast.fail(msg);
        }
      });
    },
  },
};
</script>

